body {
  margin: 0;
  padding: 0;
  /* background-color: rgb(255, 250, 240); */
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(240, 255, 240)), to(rgb(255, 250, 240)));
  background-image: -webkit-linear-gradient(top, rgb(240, 255, 240), rgb(255, 250, 240));
  background-image: -o-linear-gradient(top, rgb(240, 255, 240), rgb(255, 250, 240));
  background-image: linear-gradient(to bottom, rgb(240, 255, 240), rgb(255, 250, 240));
  font-family: 'Noto Sans', sans-serif;
  color: darkslategray;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
}



